/*       */
import { Router } from 'express';

export default class MainRouter {
                 
               

  constructor(path         = '/api')       {
    this.router = Router();
    this.path = path;
    this.init();
  }

  static index(req          , res           )       {
    res.status(200).json({
      success: 'true',
    });
  }

  init()       {
    this.router.get('/', (req          , res           ) => {
      res.status(200).json({
        success: 'true',
      });
    });
  }
}



